/* ContactPage.css */

.contact-page {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    color: aliceblue;
  }
  
  .contact-form {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .form-group {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }
  
  .form-group label {
    font-weight: bold;
  }
  
  .form-group input,
  .form-group textarea {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .form-group input:focus,
  .form-group textarea:focus {
    outline: none;
    border-color: #007bff;
  }
  
  .submit-button {
    align-self: center;
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .submit-button:hover {
    background-color: #0069d9;
  }
  
  .submit-button:active {
    background-color: #005cbf;
  }
  
  .status-message {
    margin-top: 10px;
    text-align: center;
    font-style: italic;
  }
  