.input-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

.babyphoto-container img {
    width: 200px;
    height: 200px;
    object-fit: cover;
    border-radius: 50%;
    transition: all 0.3s ease;
}

.input-container.active .babyphoto-container img {
    width: 100px;
    height: 100px;
}

.input-container input {
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    transition: all 0.3s ease;
}

.input-container.active input {
    width: 30%;
}

#result {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: center;
    align-items: center;
}

#result p {
    margin: 0;
    padding: 5px 10px;
    background-color: #f0f0f0;
    border-radius: 4px;
}
